
import { defineComponent } from 'vue'
import ProjectModel from '@/models/ProjectModel'

export default defineComponent({
  name: 'ProjectCard',
  props: {
    project: {
      type: Object as () => ProjectModel,
      required: true
    }
  },
  methods: {
    toProject () {
      this.$router.push({ name: 'projects_show', params: { id: this.project.id } })
    }
  }
})
